#tipi-result {  }
#tipi-result h2 { text-align:center; }
#tipi-result > p { box-sizing:border-box; padding-top:20px; }

#tipi-result .chart { height:160px; margin-top:20px; width:320px; }
#tipi-result .chart article { float:left; overflow:hidden; width:50%; }
#tipi-result .chart article strong { display:block; text-align:center; width:100%; }

#tipi-result #table-chart, #tipi-result #table-chart th, #tipi-result #table-chart td { border:1px solid #000; }
#tipi-result #table-chart { border-collapse:collapse; width:100%; }
#tipi-result #table-chart th:nth-of-type(1) { width:50%; }
#tipi-result #table-chart th:nth-of-type(2) { width:25%; }
#tipi-result #table-chart th:nth-of-type(3) { width:25%; }

#tipi-result .personality-description { margin-top:60px; }
#tipi-result .personality-description h3 { text-align:center; }
#tipi-result .personality-description table,
#tipi-result .personality-description table th,
#tipi-result .personality-description table td { border:1px solid #000; }
#tipi-result .personality-description table { border-collapse:collapse; width:100%; }
#tipi-result .personality-description table th { box-sizing:border-box; padding:10px; vertical-align:middle; width:30%; }
#tipi-result .personality-description table td { box-sizing:border-box; padding:10px; text-align:center; vertical-align:middle; width:70%; }

#tipi-result .circle { text-align:center; position:relative; }
#tipi-result .circle .circle-desc span { position:relative; display:block; padding-left:10px; }
#tipi-result .circle .circle-desc span:before { border-radius:14px; content:''; display:inline-block; height:8px; margin-left:-10px; position:absolute; top:50%; transform:translateY(-50%); width:8px; }
#tipi-result .circle .circle-desc span:first-child:before { background:#34DD77; }
#tipi-result .circle .circle-desc span:last-child:before { background:#A16262; }
#tipi-result .circle div { box-sizing:border-box; background:#c4c4c4; border-radius:25px; display:inline-block; margin:0 2px; overflow:hidden; position:relative; }
#tipi-result .circle .disagree-strongly { height:32px; width:32px; top:calc((32px - 50px) / 2); }
#tipi-result .circle .disagree-moderately { height:35px; width:35px; top:calc((35px - 50px) / 2); }
#tipi-result .circle .disagree-a-little { height:38px; width:38px; top:calc((38px - 50px) / 2); }
#tipi-result .circle .neither-agree-nor-disagree { height:41px; width:41px; top:calc((41px - 50px) / 2); }
#tipi-result .circle .agree-a-little { height:44px; width:44px; top:calc((44px - 50px) / 2); }
#tipi-result .circle .agree-moderately { height:47px; width:47px; top:calc((47px - 50px) / 2); }
#tipi-result .circle .agree-strongly { height:50px; width:50px; top:calc((50px - 50px) / 2); }

#tipi-result .circle .single span { height:100%; position:relative; }
#tipi-result .circle .single span strong { left:50%; position:absolute; top:50%; transform:translate(-50%, -50%); }
#tipi-result .circle div span { display:block; height:50%; position:relative; width:100%; }
#tipi-result .circle div span.up {  }
#tipi-result .circle div span.down {  }

#tipi-result .circle div span.value { background:#34DD77; }
#tipi-result .circle div span.average { background:#A16262; }

#tipi-result .circle div span strong { left:50%; transform:translateX(-50%); position:absolute; }
#tipi-result .circle div span.up strong { bottom:1px; }
#tipi-result .circle div span.down strong { top:1px; }